import React, { useState } from "react";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";
import SankeyModule from "highcharts/modules/sankey";

// Register the Sankey module
SankeyModule(Highcharts);

function App() {
  // State for managing title and data
  const [title, setTitle] = useState("Sankey Diagram Example");
  const [data, setData] = useState([
    [      "Product Sales",       "Total Revenue",       50],
    [      "Service Revenue",       "Total Revenue",      30],
    [      "Total Revenue",      "Costs",      60  ],
    [      "Total Revenue",      "Profits",      20],
    [      "Costs",      "Production Costs",      40   ],
    [      "Costs",    "Operational Costs",      20  ],
    [      "Profits",      "Net Profit",      15    ],
    [      "Profits",      "Taxes",      5    ]
  ]);

  // Event handler to update title
  const handleTitleChange = (event) => {
    setTitle(event.target.value);
  };

  // Event handler to update data
  const handleDataChange = (event) => {
    try {
      const parsedData = JSON.parse(event.target.value);
      setData(parsedData);
    } catch (error) {
      console.error("Invalid JSON format");
    }
  };

  // Options for Highcharts
  const options = {
    title: {
      text: title
    },
    exporting: {
      enabled: true
    },
    series: [
      {
        type: "sankey",
        data: data
      }
    ]
  };

  return (
    <div style={{ padding: "20px" }}>
      <h2>Interactive Sankey Diagram</h2>
      <div style={{ marginBottom: "20px" }}>
        <label>
          <strong>Title:</strong>
          <input
            type="text"
            value={title}
            onChange={handleTitleChange}
            style={{ marginLeft: "10px", padding: "5px", width: "200px" }}
          />
        </label>
      </div>
      <div style={{ marginBottom: "20px" }}>
        <label>
          <strong>Data (in JSON format):</strong>
          <textarea
            rows="12"
            cols="80"
            value={JSON.stringify(data, null, 2)}
            onChange={handleDataChange}
            style={{ marginLeft: "10px", padding: "5px" }}
          />
        </label>
      </div>
      <div style={{ marginBottom: "20px" }}>
        <button
          onClick={() => {
            // Forces component to re-render (in case needed)
            setData([...data]);
          }}
          style={{ padding: "10px 20px", cursor: "pointer" }}
        >
          Generate Diagram
        </button>
      </div>
      <div>
        <HighchartsReact highcharts={Highcharts} options={options} />
      </div>
    </div>
  );
}

export default App;
